import * as types from './mutation-types';

export default {
	fetchHistory_: ({ state, commit, dispatch }, payload) => {
		return;
	},

	closeModal_({ commit }) {
		commit(types.SET_STATE, {
			key: 'GuideBoardModalValue',
			value: {
				flg: 'I',
				boardNo: '',
				category: '',
				dtlCategory: '',
				title: '',
				regId: '',
				conts: `1. 신청 항목 종류 : 

2. 구매 링크 : 

3. 상세 내용 : `
			}
		});

		commit(types.SET_STATE, {
			key: 'GuideBoardModal',
			value: false
		});
	}
};
