<template>
  <sf-search-layout>
    <el-row :gutter="20">
      <el-col :span="4">
        <sf-select-group
          :label="Label"
          :inputValue="systemId"
          :options="systemIds_"
          :onChange="value => onChange({ key: 'systemId', value })"
        />
      </el-col>
    </el-row>
    <template slot="button">
      <sf-button line :onClick="findAllProcesses"><i class="icon-sf-search"></i>조회</sf-button>
    </template>
  </sf-search-layout>
</template>

<script>
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/VerificationHistory/store/helpers';
export default {
  computed: {
    ...rootComputed,
    ...serviceComputed
  },
  methods: {
    ...rootMethods,
    ...serviceMethods,
    // onChange({ key, value }) {
    //   this.DEFAULT_ASSIGN({
    //     key: 'searchParams',
    //     value: {
    //       ...this.searchParams,
    //       [key]: value
    //     }
    //   });
    // },
    onChange({ key, value }) {
      this.systemId = value;
    },
    findAllProcesses() {
			this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ }).finally(() =>
				this.$sf_toggleLoading(false)
			);
    }
  },
  data() {
    return {
      Label : '시스템 ID',
      systemId: 'ALL',
      systemIds_: [
			{
				label: 'MES',
				value: 0,
				disabled: false
			},
			{
				label: 'WPMS',
				value: 1,
				disabled: false
			},
			{
				label: 'HALO',
				value: 2,
				disabled: false
			},
			{
				label: 'HALO DEPOT',
				value: 3,
				disabled: false
			},
			{
				label: 'Q-PAS',
				value: 4,
				disabled: false
			}
		]
    }
  }
}
</script>

<style langType="scss"></style>