export default {
	systemIds_: state => {
		const { systemIds } = state.selectOptions;
		return [...systemIds];
	},

	plantCd_: state => {
		const { plantCd } = state.searchParams;
		return plantCd;
	},

	systemId_: state => {
		const { systemId } = state.searchParams;
		return systemId;
	},

	historyData_: state => {
		const { historyData } = state;
		return historyData;
	}
};
