<template>
  <sf-data-table
    :columns="columns"
    :rows="rows"
    index
    height="100%"
    paging
    :pageUnit="pageUnit_"
    :pageArray="screen.pageArray"
    :totalSize="totalSize_"
    @changedPage="changedPage"
  >
  </sf-data-table>
</template>

<script>
import columns from './columns';
import { rootComputed, rootMethods } from '@/store/common/helpers';
import { serviceComputed, serviceMethods } from '@common/VerificationHistory/store/helpers';

export default {
  computed: {
    ...rootComputed,
    ...serviceComputed,
    columns() {
      return columns.map(column => ({ ...column}));
    },
    // rows() {
    //   return this.historyData_.map(history => ({
    //     ...history
    //   }))
    // }
  },
  methods: {
    ...rootMethods,
    ...serviceMethods,

    changedPage({ currentPageVal, pageUnitVal }) {
      Promise.resolve()
      .then(() => 
      this.DEFAULT_ASSIGN({ key: 'pagingInfo', value: { ...this.pagingInfo, currentPage: currentPageVal, pageUnit: pageUnitVal }})
      )
    },
    // findAllProcesses() {
		// 	this.fetchHistory_({ urlSearchParams: this.urlSearchParams_ }).finally(() =>
		// 		this.$sf_toggleLoading(false)
    //   );
    // }
  }
}
</script>

